<template>
    <div class="aside">
        <!-- 使用指令 v-link 进行导航。 -->
        <d-nav-menu>
            <d-nav-menu-item v-for="menu in menus" :text="menu.text" :path="menu.path" :expanded="$index === 0">
            </d-nav-menu-item>
