<style>
    .main {
        position: relative;
        margin-left: 150px;
        padding-top: 20px;
        height: 1000px;
        box-sizing: border-box;
        overflow: auto;
    }

    .aside {
        position: absolute;
        padding-top: 20px;
        width: 150px;
        z-index: 1;
    }

    .aside ul {
        margin: 0;
        padding: 0;
        width: 100%;
        list-style: none;
        background-color: transparent;
        font-size: 12px;
    }

    .aside ul li {
