        background-color: #FFF;
        text-align: center;
        line-height: 50px;
    }

    .aside ul li a {
        text-decoration: none;
        color: #000;
    }
</style>

<script>
    import store from '../../store/store'


    export default{
        data(){
            return {
                menus: [{
                    text: '壁纸分类',
                    path: '/CommonSetting/bizhiFenlei'
                },{
                    text:'壁纸分辨率',
                    path:'/CommonSetting/bizhiFenbianlv'
                },{
                    text:'壁纸色系',
                    path:'/CommonSetting/bizhiSex'
                },{
                    text:'幻灯片管理',
                    path:'/CommonSetting/PPTguanli'
                }]
            }
        },
        store:store
    }
</script>


