import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
    ZhuanJi:'',
    colornm:'',
    PPTnm:'',
    isEdit:Boolean()
}

const mutations = {

    //编辑专辑名称
    EDITZHUANJI (state, name,status) {
        state.ZhuanJi = name;
        state.isEdit = status;
    },

    //编辑颜色
    EDITCOLOR (state,color,status){
        state.colornm = color;
        state.isEdit = status
    },

    //编辑幻灯片
    EDITPPT(state,PPT,status){
        state.PPTnm = PPT;
        state.isEdit = status;
    }

}


export default new Vuex.Store({
    state,
    mutations
})


