<template>
    <div :style="exampleStyle"></div>
    <div class="color-choose">
        <div v-for="items in colorArrs" class="color-cell-tr">
            <div v-for="item in items" class="color-cell-td" :style="item" @click="con($event)"></div>
        </div>
    </div>



</template>

<style>

    .color-choose {
        margin-top:10px;
        width:235px;
        height:159px;
    }

    .color-cell-tr {
        float: left;
        width:78px;
        height:79px;
    }

    .color-cell-td {
        float: left;
        width:12px;
        height:12px;
        cursor: pointer;
        border-bottom: 1px solid #FFF;
        border-right:1px solid #FFF;
    }
    .color-cell-tr:nth-child(3n){
        width:79px;
    }
    .color-cell-tr:nth-child(4n).color-cell-td:nth-child(7n){
        border-left:1px solid #FFF;

    }

</style>


<script>
    var colorArrs = [];
    for (var i = 0;i<6;i++){
        colorArrs[i] = [];
        for (var j = 0;j<6;j++){
            for (var k=0;k<6;k++){
                colorArrs[i].push({backgroundColor:'rgb('+i*51+','+j*51+','+k*51+')'})
            }
        }
    }

    export default {
        data(){
            return {
                colorArrs:colorArrs,
                exampleStyle:{
                    marginTop:'5px',
                    width:"24px",
                    height:"24px",
                    border:"1px solid #FFF",
                    backgroundColor:"#FFF"
                }
            }
        },
        methods:{
            con(e){
                var target  = e.target;
                var color = target.style.backgroundColor;

                return (this.exampleColor(color));
            },
            exampleColor(color){
                this.exampleStyle.backgroundColor = color;
            }
        }
    }


</script>


