var Vue = require('vue');

require('vue-desktop');

var VueRouter = require('vue-router');

Vue.use(VueRouter);

/*Vue.component('app', require('./src/zhuanji.vue'));
 Vue.component('vform',require('./src/addBizhi.vue'));
 Vue.component('grid-child',require('./src/bizhiGuanli.vue'));
 Vue.component('grid-child2',require('./src/wenjianGuanli.vue'));

 new Vue({
 el:'body'
 })*/

var App = Vue.extend({})

var router = new VueRouter({
    history: false
})

// 定义路由规则
// 每条路由规则应该映射到一个组件。这里的“组件”可以是一个使用 Vue.extend
// 创建的组件构造函数，也可以是一个组件选项对象。
// 稍后我们会讲解嵌套路由

var delay_t= 0;

router.map({
    '/PCbizhi': {
        component: require('./src/PCbizhi/PCbizhi.vue'),
        subRoutes: {
            '/app': {
                component: function (resolve) {
                    setTimeout(function () {
                        require(['./src/PCbizhi/zhuanji.vue'], resolve)
                    }, delay_t)
                }
            },
            '/v-f': {
                component: function (resolve) {
                    setTimeout(function () {
                        require(['./src/PCbizhi/addBizhi.vue'], resolve)
                    }, delay_t)
                }
            },
            '/grid-child1': {
                component: function (resolve) {
                    setTimeout(function () {
                        require(['./src/PCbizhi/bizhiGuanli.vue'], resolve)
                    }, delay_t)
                }
            },
            '/grid-child2': {
                component: function (resolve) {
                    setTimeout(function () {
                        require(['./src/PCbizhi/wenjianGuanli.vue'], resolve)
                    }, delay_t)
                }
            },
            '/b-upload1': {
                component: function (resolve) {
                    setTimeout(function () {
                        require(['./src/PCbizhi/uploadPC.vue'], resolve)
                    }, delay_t)
                }
            }
        }
    },
    '/MOBILEbizhi':{
        component:require('./src/MOBILEbizhi/MOBILEbizhi.vue'),
        subRoutes:{
            '/ZHUANJI':{
                component:function(resolve){
                    setTimeout(function () {
                        require(['./src/MOBILEbizhi/ZHUANJI.vue'], resolve)
                    }, delay_t)
                }
            },
            '/editZJ': {
                component: function (resolve) {
                    setTimeout(function () {
                        require(['./src/MOBILEbizhi/v-f.vue'], resolve)
                    }, delay_t)
                }
            },
            '/bizhiGuanli': {
                component: function (resolve) {
                    setTimeout(function () {
                        require(['./src/MOBILEbizhi/bizhiGuanli.vue'], resolve)
                    }, delay_t)
                }
            },
            '/wenjianGuanli': {
                component: function (resolve) {
                    setTimeout(function () {
                        require(['./src/MOBILEbizhi/wenjianGuanli.vue'], resolve)
                    }, delay_t)
                }
            },
            '/b-upload1': {
                component: function (resolve) {
                    setTimeout(function () {
                        require(['./src/MOBILEbizhi/v-f.vue'], resolve)
                    }, delay_t)
                }
            },
            '/b-upload2':{
                component: function (resolve) {
                    setTimeout(function () {
                        require(['./src/MOBILEbizhi/b-upload.vue'], resolve)
                    }, delay_t)
                }
            }
        }
    },
    '/CommonSetting':{
        component:require('./src/CommonSetting/CommonSetting.vue'),
        subRoutes:{
            '/bizhiFenlei':{
                component:function(resolve){
                    setTimeout(function () {
                        require(['./src/CommonSetting/bizhiFenlei.vue'], resolve)
                    }, delay_t)
                }
            },
            '/zileiGuanli':{
                component:function(resolve){
                    setTimeout(function () {
                        require(['./src/CommonSetting/zileiGuanli.vue'], resolve)
                    }, delay_t)
                }
            },
            '/bizhiFenbianlv':{
                component:function(resolve){
                    setTimeout(function () {
                        require(['./src/CommonSetting/bizhiFenbianlv.vue'], resolve)
                    }, delay_t)
                }
            },
            '/addSize':{
                component:function(resolve){
                    setTimeout(function () {
                        require(['./src/CommonSetting/addSize.vue'], resolve)
                    }, delay_t)
                }
            },
            '/bizhiSex':{
                component:function(resolve){
                    setTimeout(function () {
                        require(['./src/CommonSetting/bizhiSex.vue'], resolve)
                    }, delay_t)
                }
            },
            'addColor':{
                component:function(resolve){
                    setTimeout(function () {
                        require(['./src/CommonSetting/addColor.vue'], resolve)
                    }, delay_t)
                }
            },
            'PPTguanli':{
                component:function(resolve){
                    setTimeout(function () {
                        require(['./src/CommonSetting/PPTguanli.vue'], resolve)
                    }, delay_t)
                }
            },
            'addPPT':{
                component:function(resolve){
                    setTimeout(function () {
                        require(['./src/CommonSetting/addPPT.vue'], resolve)
                    }, delay_t)
                }
            }
        }
    }

})

// 现在我们可以启动应用了！
// 路由器会创建一个 App 实例，并且挂载到选择符 #app 匹配的元素上。
router.start(App, 'body')


